<style lang="scss" >
.video-new {
  position: relative;
  width: 100vw;
  height: 100vh;
  z-index: 999;
  padding-top: 46px;
  background: var(--theme-background-black);
  // 头部tab 部分
  .tab {
    background: rgba(243, 237, 237, 0.1);
    .van-nav-bar__title {
      color: #fff;
    }
  }

  // 视频部分
  .give {
    position: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    padding: 10px;
    right: 0px;
    top: 350px;
    width: 50px;
    height: 150px;
    background: rgba(242, 240, 240, 0.1);
    z-index: 99999;
  }
  .star {
    .container {
      /* 鼠标移入,光标变小手 */
      cursor: pointer;
      /* 自定义属性,可通过var函数对其调用 */
      --c: #ff6b81;
    }
    svg {
      width: 30px;
      /* 溢出可见 */
      overflow: visible;
      /* 相对定位 */
      position: relative;
      z-index: 10;
    }
    #heart {
      fill: #eee;
      /* stroke属性,可应用于任何种类的线条,文字和元素,就像一个圆的轮廓 */
      stroke: var(--c);
      /* 线条宽度 */
      stroke-width: 40px;
      /* 设置线条为虚线,虚线的长度 */
      stroke-dasharray: 2600;
      /* 虚线的位移 */
      stroke-dashoffset: 2600;
      /* 端点为圆头 */
      stroke-linecap: round;
    }
    span {
      display: block;
      width: 12px;
      height: 12px;
      background-color: transparent;
      border-radius: 50%;
      /* 绝对定位 居中 */
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) scale(0);
      /* 设置各个方向的阴影 */
      box-shadow: 0 -24px 0 var(--c), 0 24px 0 var(--c), -24px 0 0 var(--c),
        24px 0 0 var(--c), -18px -18px 0 var(--c), 18px -18px 0 var(--c),
        18px 18px 0 var(--c), -18px 18px 0 var(--c);
    }
    #checkbox:checked + svg #heart {
      /* 执行动画：动画名 时长 线性的 停留在最后一帧 */
      animation: drawHeart 1s linear forwards;
    }
    #checkbox:checked ~ span {
      /* 执行动画：动画名 时长 加速后减速 停留在最后一帧 */
      animation: blink 0.5s ease-in-out forwards;
      /* 动画延迟时间 */
      animation-delay: 0.85s;
    }
    #checkbox:checked + svg {
      /* 执行动画：动画名 时长 线性的 停留在最后一帧 */
      animation: beat 1s linear forwards;
    }

    /* 定义动画 */
    /* 画心的动画 */
    @keyframes drawHeart {
      0% {
        stroke: var(--c);
        stroke-dashoffset: 2600;
      }
      80% {
        stroke: var(--c);
        stroke-dashoffset: 0;
        fill: #eee;
      }
      100% {
        stroke: var(--c);
        stroke-dashoffset: 0;
        fill: var(--c);
      }
    }
    /* 小圆点闪出的动画 */
    // @keyframes blink {
    //     0%{
    //         transform: translate(-50%,-50%) scale(0.5);
    //         opacity: 0.8;
    //     }
    //     50%{
    //         transform: translate(-50%,-50%) scale(1);
    //         opacity: 1;
    //     }
    //     100%{
    //         transform: translate(-50%,-50%) scale(1.1);
    //         opacity: 0;
    //     }
    // }
    /* 心跳动的动画 */
    @keyframes beat {
      0% {
        transform: scale(1);
      }
      70% {
        transform: scale(1);
      }
      80% {
        transform: scale(1.5);
      }
      100% {
        transform: scale(1);
      }
    }
  }
  .commen {
  }
}
</style>